






















import { Component, Vue } from 'vue-property-decorator';
import { from } from 'rxjs'
import { ajax } from "rxjs/ajax"
import { map, mergeMap, defaultIfEmpty, catchError } from 'rxjs/operators'

@Component
export default class Home extends Vue {
