    const symbol = this.$route.params.symbol.toUpperCase()
    this.$store.dispatch('nav/update', {
      symbol,
      company: [
        {
            name: 'company keystat',
            title: 'Overview'
          },
          {
            name: 'company news',
            title: 'News'
          },
          {
            name: 'company chart',
            title: 'Chart',
          },
          {
            name: 'company earnings',
            title: 'Earnings'
          },
      ]
    })
  }
}
