import { State } from '../../types/Nav'

const state: State = {
  list: [
    { title: 'Search', url: '/stocks/search' },
    { title: 'Watchlist', url: '/stocks/watchlist' },
    { title: 'Leaderboard', url: '/stocks/leaderboard' },
  ],
  symbol: '',
  company: []
}


const actions = {
  update(store: any, data: any) {
    const { commit } = store
    commit('addNav', data)
  }
}

const mutations = {
  addNav(state: any, data: any) {
    const { symbol, list, company } = data
    if (symbol) {
      state.symbol = symbol
    } else {
      state.symbol = ''
    }

    if (company) {
      state.company = Object.assign({}, company)
    }
  }
}

export default {
  namespaced: true,
  state,
  actions,
  mutations
}