









































import { Component, Vue, Watch } from 'vue-property-decorator'
import { mapState, mapActions } from 'vuex'
import { Nav } from './types/Nav'

@Component
export default class App extends Vue {
  toggle!: string

  @Watch('$route')
  onRouteChanged() {
    this.toggle = 'is-hidden-touch'
  }

  data() {
    return {
      toggle: 'is-hidden-touch'
    }
  }

  get navs() {
    return this.$store.state.nav.list
  }

  get company() {
    return this.$store.state.nav.company
  }

  get symbol() {
    return this.$store.state.nav.symbol
  }

  toggleNav() {
    this.toggle = this.toggle === '' ? 'is-hidden-touch' : ''
  }
}
